import { Plugin } from '@ali/4ever-cangjie';
import { selectSubjectRange } from '../commands';
import { SELECTED_SUBJECT_KEY } from '../constants';
import { hasSubjectBeenSelected } from '../helpers';
import { isSubjectHeading } from '../utils';
import type { Subject } from '../utils';

function createOnCangjiePaste(): Plugin['onCangjiePaste'] {
  return function onCangjiePaste(event, controller, next) {
    if (hasSubjectBeenSelected(controller)) {
      const { document, data } = controller.value;
      const { [SELECTED_SUBJECT_KEY]: selectedKey } = data;
      const block = document.getNode(selectedKey);
      if (isSubjectHeading(block)) {
        // Paste 之前先全选议题
        controller.command(selectSubjectRange, block as Subject);
      }
    }
    return next();
  };
}

export default createOnCangjiePaste;
